.button {
    background-color: #800000;
    color: #ffffff;
    border-radius: 0;
    padding: 10px 20px;
    font-size: 16px;
    border: 2px solid #fff;
    margin-top: 30px;
}

.button:hover,
.button:active {
    background-color: #972e2e;
    outline: none;
    cursor: pointer;
}


